'use client';

import React, { useEffect, useMemo, useState } from 'react';
import { useParams, useRouter, useSearchParams } from 'next/navigation';
import { createClient } from '../../../../../../lib/supabaseBrowser';
import Topbar from '../../../../../../components/Topbar';
import Button from '../../../../../../components/Button';

type StudentRow = {
  id: string;
  name: string;
  public_slug: string | null;
  email: string | null;
  p1k_sec_per_km: number | null;
};

type WorkoutRow = {
  id: string;
  student_id: string;
  trainer_id: string;
  status: 'draft' | 'ready' | 'archived';
  template_type: 'easy_run' | 'progressive' | 'alternated';
  include_warmup: boolean;
  warmup_km: number;
  include_cooldown: boolean;
  cooldown_km: number;
  blocks: any[];
  total_km: number;
  share_slug: string | null;
  created_at: string;
  title: string | null;
};

type WeekRow = { id: string; week_start: string; week_end: string; label: string | null; };


type BlockDraft = {
  id: string;
  distanceKm: string; // string p/ permitir vazio ao digitar
  intensity: 'leve' | 'moderado' | 'forte';
};

const TEMPLATE_LABEL: Record<string, string> = {
  easy_run: 'Rodagem',
  progressive: 'Progressivo',
  alternated: 'Alternado',
};

function uid() {
  return Math.random().toString(16).slice(2);
}

function slugify(s: string) {
  return (s || '')
    .normalize('NFD')
    .replace(/[\u0300-\u036f]/g, '')
    .toLowerCase()
    .trim()
    .replace(/[^a-z0-9]+/g, '-')
    .replace(/(^-|-$)+/g, '');
}

function makeStudentSlug(name: string, id: string) {
  const base = slugify(name) || 'aluno';
  const suffix = (id || '').replace(/-/g, '').slice(0, 6) || '000000';
  return `${base}-${suffix}`;
}

function formatPace(secPerKm: number | null) {
  if (!secPerKm || !Number.isFinite(secPerKm)) return '—';
  const total = Math.max(0, Math.floor(secPerKm));
  const mm = Math.floor(total / 60);
  const ss = total % 60;
  return `${mm}:${ss.toString().padStart(2, '0')}`;
}

function parseKm(str: string, min: number) {
  const cleaned = (str || '').replace(',', '.');
  const n = Number(cleaned);
  if (!Number.isFinite(n)) return min;
  return Math.max(min, n);
}

export default function NewWorkoutPage() {
  const router = useRouter();
  const params = useParams<{ id: string }>();
  const studentId = params.id;
  const searchParams = useSearchParams();
  const weekId = searchParams.get('weekId');

  const supabase = useMemo(() => createClient(), []);

  const [loading, setLoading] = useState(true);
  const [banner, setBanner] = useState<string | null>(null);

  const [student, setStudent] = useState<StudentRow | null>(null);
  const [week, setWeek] = useState<WeekRow | null>(null);
  const [title, setTitle] = useState('');
  const [plannedDate, setPlannedDate] = useState<string>('');

  const [templateType, setTemplateType] = useState<'easy_run' | 'progressive' | 'alternated'>('easy_run');

  const [includeWarmup, setIncludeWarmup] = useState(true);
  const [warmupKm, setWarmupKm] = useState('2');

  const [includeCooldown, setIncludeCooldown] = useState(true);
  const [cooldownKm, setCooldownKm] = useState('1');

  const [blocks, setBlocks] = useState<BlockDraft[]>([
    { id: uid(), distanceKm: '6', intensity: 'leve' },
  ]);

  const [saving, setSaving] = useState(false);
  const [createdWorkoutId, setCreatedWorkoutId] = useState<string | null>(null);

  useEffect(() => {
    let alive = true;

    async function load() {
      setLoading(true);
      setBanner(null);

      const {
        data: { user },
      } = await supabase.auth.getUser();

      if (!user) {
        const next = window.location.pathname + window.location.search;
        router.replace(`/login?next=${encodeURIComponent(next)}`);
        return;
      }

      const { data: st, error } = await supabase
        .from('students')
        .select('id,name,public_slug,email,p1k_sec_per_km')
        .eq('id', studentId)
        .maybeSingle();

      if (!alive) return;

      if (error) {
        setBanner(error.message);
        setLoading(false);
        return;
      }

      if (!st) {
        setBanner('Aluno não encontrado (ou sem permissão).');
        setLoading(false);
        return;
      }

      setStudent(st as StudentRow);

if (weekId) {
  const { data: wk } = await supabase
    .from('weeks')
    .select('id, week_start, week_end, label')
    .eq('id', weekId)
    .maybeSingle();
  if (alive) setWeek((wk as any) || null);
} else {
  if (alive) setWeek(null);
}

      setLoading(false);
    }

    void load();

    return () => {
      alive = false;
    };
  }, [router, studentId, supabase]);

  function normalizedWarmup() {
    // mantém >0 mesmo com includeWarmup=false por causa de constraint típica
    if (!includeWarmup) return 1;
    return parseKm(warmupKm, 0.5);
  }

  function normalizedCooldown() {
    if (!includeCooldown) return 1;
    return parseKm(cooldownKm, 0.5);
  }

  function buildBlocksPayload() {
    return blocks.map((b, idx) => ({
      index: idx + 1,
      distance_km: parseKm(b.distanceKm, 0.5),
      intensity: b.intensity,
    }));
  }

  function totalKmEstimate() {
    const warm = includeWarmup ? normalizedWarmup() : 0;
    const cool = includeCooldown ? normalizedCooldown() : 0;
    const sumBlocks = buildBlocksPayload().reduce((acc, x) => acc + (x.distance_km || 0), 0);
    return Math.round((warm + sumBlocks + cool) * 10) / 10;
  }

  function addBlock() {
    setBlocks((prev) => [...prev, { id: uid(), distanceKm: '1', intensity: 'moderado' }]);
  }

  function removeBlock(id: string) {
    setBlocks((prev) => prev.filter((x) => x.id !== id));
  }

  function updateBlock(id: string, patch: Partial<BlockDraft>) {
    setBlocks((prev) => prev.map((x) => (x.id === id ? { ...x, ...patch } : x)));
  }

  async function insertWorkout(status: 'draft' | 'ready') {
    if (!student) return;

    setSaving(true);
    setBanner(null);

    try {
      const payloadBase: any = {
        student_id: student.id,
        week_id: weekId || null,
        status,
        planned_date: plannedDate ? plannedDate : (week?.week_start ?? null),
        template_type: templateType,
        title: title.trim() || null,
        include_warmup: includeWarmup,
        warmup_km: normalizedWarmup(),
        include_cooldown: includeCooldown,
        cooldown_km: normalizedCooldown(),
        blocks: buildBlocksPayload(),
        total_km: totalKmEstimate(),
      };

      const { data, error } = await supabase
        .from('workouts')
        .insert(payloadBase)
        .select('id,share_slug,status')
        .maybeSingle();

      if (error) throw error;

      if (!data?.id) {
        setBanner('Não foi possível criar o treino (id vazio).');
        return;
      }

      setCreatedWorkoutId(data.id);
      if (status === 'ready') {
        await handleShare(data.id);
      } else {
        setBanner('Rascunho criado.');
      }
    } catch (e: any) {
      setBanner(e?.message ? `Erro: ${e.message}` : 'Erro ao criar treino.');
    } finally {
      setSaving(false);
    }
  }

  async function handleShare(workoutId: string) {
    if (!student) return;
    setBanner(null);

    // Garante status=ready para trigger gerar share_slug
    const { data, error } = await supabase
      .from('workouts')
      .update({ status: 'ready' })
      .eq('id', workoutId)
      .select('id,share_slug')
      .maybeSingle();

    if (error) {
      setBanner(error.message);
      return;
    }

    const shareSlug = data?.share_slug;
    if (!shareSlug) {
      setBanner('share_slug vazio (trigger não gerou).');
      return;
    }

    const studentSlug =
      student.public_slug || makeStudentSlug(student.name, student.id);

    const url = `${window.location.origin}/w/${studentSlug}/${shareSlug}`;

    try {
      await navigator.clipboard.writeText(url);
      setBanner('Link copiado!');
    } catch {
      setBanner(`Link: ${url}`);
    }
  }

  return (
    <>
      <Topbar title="Novo treino" />
      <main className="flex-1 p-4">
        {loading ? (
          <div className="text-sm text-slate-600 dark:text-slate-300">Carregando…</div>
        ) : banner ? (
          <div className="rounded-xl bg-amber-50 dark:bg-amber-900/20 p-4 text-sm text-amber-800 dark:text-amber-200">
            {banner}
          </div>
        ) : null}

        <div className="mx-auto max-w-3xl space-y-4">
          <div className="rounded-2xl bg-white dark:bg-surface-dark p-4 shadow">
            <div className="text-sm text-slate-600 dark:text-slate-300">Aluno</div>
            <div className="text-xl font-semibold">{student?.name}</div>
            <div className="mt-1 text-sm text-slate-600 dark:text-slate-300">
              P1k: <span className="font-semibold">{formatPace(student?.p1k_sec_per_km ?? null)}</span>
              {student?.email ? <> · {student.email}</> : null}
            </div>
          </div>

          <div className="rounded-2xl bg-white dark:bg-surface-dark p-4 shadow">
            <div className="font-semibold">Título do treino</div>
            <input
              value={title}
              onChange={(e) => setTitle(e.target.value)}
              placeholder="Ex: Rodagem leve"
              className="mt-3 w-full rounded-xl border border-slate-300 dark:border-slate-700 bg-white dark:bg-slate-800 px-4 py-3"
            />

            <div className="mt-4 grid grid-cols-1 sm:grid-cols-2 gap-3">
              <div>
                <div className="text-sm text-slate-600 dark:text-slate-300">Template</div>
                <select
                  value={templateType}
                  onChange={(e) => setTemplateType(e.target.value as any)}
                  className="mt-2 w-full rounded-xl border border-slate-300 dark:border-slate-700 bg-white dark:bg-slate-800 px-4 py-3"
                >
                  <option value="easy_run">Rodagem</option>
                  <option value="progressive">Progressivo</option>
                  <option value="alternated">Alternado</option>
                </select>
              </div>

              <div>
                <div className="text-sm text-slate-600 dark:text-slate-300">Data planejada (opcional)</div>
                <input
                  type="date"
                  value={plannedDate}
                  onChange={(e) => setPlannedDate(e.target.value)}
                  className="mt-2 w-full rounded-xl border border-slate-300 dark:border-slate-700 bg-white dark:bg-slate-800 px-4 py-3"
                />
              </div>
            </div>

            <div className="mt-4 text-sm text-slate-600 dark:text-slate-300">
              Distância total estimada: <span className="font-semibold">{totalKmEstimate()} km</span>
            </div>
          </div>

          <div className="rounded-2xl bg-white dark:bg-surface-dark p-4 shadow space-y-3">
            <div className="flex items-center justify-between">
              <div className="font-semibold">Aquecimento</div>
              <label className="inline-flex items-center gap-2 text-sm">
                <input
                  type="checkbox"
                  checked={includeWarmup}
                  onChange={() => setIncludeWarmup((v) => !v)}
                />
                Incluir
              </label>
            </div>

            <input
              value={warmupKm}
              onChange={(e) => setWarmupKm(e.target.value)}
              disabled={!includeWarmup}
              inputMode="decimal"
              className="w-full rounded-xl border border-slate-300 dark:border-slate-700 bg-white dark:bg-slate-800 px-4 py-3 disabled:opacity-50"
              placeholder="Km (ex: 2)"
            />
          </div>

          <div className="rounded-2xl bg-white dark:bg-surface-dark p-4 shadow space-y-3">
            <div className="flex items-center justify-between">
              <div className="font-semibold">Blocos</div>
              <button className="text-sm underline" onClick={addBlock}>
                + Adicionar bloco
              </button>
            </div>

            <div className="space-y-3">
              {blocks.map((b, idx) => (
                <div key={b.id} className="rounded-xl border border-slate-200 dark:border-slate-700 p-3">
                  <div className="flex items-center justify-between">
                    <div className="font-medium">Bloco {idx + 1}</div>
                    {blocks.length > 1 ? (
                      <button className="text-sm underline text-red-600" onClick={() => removeBlock(b.id)}>
                        Remover
                      </button>
                    ) : null}
                  </div>

                  <div className="mt-3 grid grid-cols-1 sm:grid-cols-2 gap-3">
                    <div>
                      <div className="text-sm text-slate-600 dark:text-slate-300">Distância (km)</div>
                      <input
                        value={b.distanceKm}
                        onChange={(e) => updateBlock(b.id, { distanceKm: e.target.value })}
                        inputMode="decimal"
                        className="mt-2 w-full rounded-xl border border-slate-300 dark:border-slate-700 bg-white dark:bg-slate-800 px-4 py-3"
                      />
                    </div>

                    <div>
                      <div className="text-sm text-slate-600 dark:text-slate-300">Intensidade</div>
                      <div className="mt-2 flex gap-2">
                        {(['leve', 'moderado', 'forte'] as const).map((opt) => (
                          <button
                            key={opt}
                            type="button"
                            onClick={() => updateBlock(b.id, { intensity: opt })}
                            className={`flex-1 px-3 py-2 rounded-lg text-sm font-semibold whitespace-nowrap ${
                              b.intensity === opt
                                ? 'bg-primary text-slate-900'
                                : 'bg-slate-100 dark:bg-slate-800 text-slate-800 dark:text-slate-100'
                            }`}
                          >
                            {opt === 'leve' ? 'Leve' : opt === 'moderado' ? 'Moderado' : 'Forte'}
                          </button>
                        ))}
                      </div>
                    </div>
                  </div>
                </div>
              ))}
            </div>
          </div>

          <div className="rounded-2xl bg-white dark:bg-surface-dark p-4 shadow space-y-3">
            <div className="flex items-center justify-between">
              <div className="font-semibold">Desaquecimento</div>
              <label className="inline-flex items-center gap-2 text-sm">
                <input
                  type="checkbox"
                  checked={includeCooldown}
                  onChange={() => setIncludeCooldown((v) => !v)}
                />
                Incluir
              </label>
            </div>

            <input
              value={cooldownKm}
              onChange={(e) => setCooldownKm(e.target.value)}
              disabled={!includeCooldown}
              inputMode="decimal"
              className="w-full rounded-xl border border-slate-300 dark:border-slate-700 bg-white dark:bg-slate-800 px-4 py-3 disabled:opacity-50"
              placeholder="Km (ex: 1)"
            />
          </div>

          <div className="flex flex-col sm:flex-row gap-2">
            <Button onClick={() => insertWorkout('draft')} disabled={saving} fullWidth>
              Salvar rascunho
            </Button>
            <Button onClick={() => insertWorkout('ready')} disabled={saving} fullWidth>
              Salvar e copiar link
            </Button>
          </div>

          {createdWorkoutId ? (
            <button
              className="text-sm underline text-slate-600 dark:text-slate-300"
              onClick={() => router.push(`/students/${studentId}`)}
            >
              Voltar para o aluno
            </button>
          ) : (
            <button className="text-sm underline text-slate-600 dark:text-slate-300" onClick={() => router.push(`/students/${studentId}`)}>
              Cancelar
            </button>
          )}
        </div>
      </main>
    </>
  );
}
